import { newSpecPage } from '@stencil/core/testing';
import { PrestoYoutube } from '../presto-youtube';

describe('presto-youtube', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoYoutube],
      html: `<presto-youtube src="https://www.youtube.com/watch?v=OGKWgxTL2zI"></presto-youtube>`,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('renders lazy load', async () => {
    const page = await newSpecPage({
      components: [PrestoYoutube],
      html: `<presto-youtube lazy-load src="https://www.youtube.com/watch?v=OGKWgxTL2zI"></presto-youtube>`,
    });
    expect(page.root).toMatchSnapshot();
  });
});
